.nav {
  display: flex;
  padding: 0;

  list-style: none;

  &__item {
    margin-left: 20px;

    &:first-child {
      margin-left: 0;
    }
  }

  &__link {
    position: relative;
    display: flex;
    align-items: center;
    height: 60px;

    font-size: 12px;
    text-decoration: none;
    text-transform: uppercase;
    font-weight: 500;
    color: $main-text-color;
  }

  &__link:hover {
    color: $active-color;
  }

  &__link--is-active {
    color: $active-color;

    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;

      height: 4px;

      background-color: $active-color;
      border-radius: 4px;
    }
  }

}
